*{
 margin: 0;
 padding: 0;   
}
html{
    background-image: url('./Pics/bright.jpg');
    background-size: cover;
    background-position: top;   
}
main{
    min-height: 100vh;
    background-image:linear-gradient(to bottom, rgba(0,0,0,3), rgba(0,0,0,2 ) );
}
.enter{
    display: block;
    padding: 5%;
}
.search{
    height: 1cm;
    width: 5cm;
    border-radius: 3px 6px 3px 6px;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.5);
}
.find:hover{
    background-color: darkcyan;
}
.find{
    height: 1.1cm;
    width: 2cm;
    border-radius: 3px 3px 6px 6px;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.5);
    background-color: cyan;
}
.place{
    color: brown;
    font-size: 75px;
    font-weight: 600;
    text-shadow: 3px 5px rgba(0, 0, 0, 0.5);
    margin-bottom: 1cm ;
}
.date{
    font-size: 35px;
    font-weight: 500;
    text-shadow: 3px 3px rgba(0, 0, 0, 0.5);
    color: darkred;
    margin-bottom: 2cm;
}
.percentage{
    background-color: rgba(300, 300, 300, 0.4);
    padding: 15px 25px;
    width: 7cm;
    margin: 30px auto;
    font-size: 100px;
    font-weight: 300;
    border-radius: 10px;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.5);
    text-shadow: 3px 3px rgba(0, 0, 0, 0.5);
}
.temperature{
    font-size: 30px;
    font-weight: 400;
    text-shadow: 3px 3px rgba(0, 0, 0, 0.5);
}
.picture{
    width: 4cm;
    height: 4cm;
}